

/* Google Fonts: Noto Sans KR */
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');

/* Font variables */
$font-body: 'Noto Sans KR',
             -apple-system,
             BlinkMacSystemFont,
             'Apple SD Gothic Neo',
             'Malgun Gothic',
             Arial,
             sans-serif;

$font-heading: $font-body;

/* Apply fonts */
body {
  font-family: $font-body;
}
